#logo {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

#instructions {
  border-radius: 25px;
  border: 5px solid grey;
  vertical-align: middle;
  height: 200px;
  width: 450px;
  margin: 0 auto;
}
section {
    background-color: azure;
}
.wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
  }
.card {
    width: 150px;
    margin: 10px auto;
    border: 1px solid black;
    background-color: hsla(0, 100%, 71%, 0.486);
    border-radius: 20px;
  }
body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  text-align: center;
}
